
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Alert from 'vux/src/components/alert/index.vue'

export default {
components: {
Swiper,
SwiperItem,
XButton,
Alert
},
data() {
return {
demo04_list: [
{
src: require("@/assets/imgs/banner1.jpg"),
title: "图片1"
},
{
src: require("@/assets/imgs/banner2.jpg"),
title: "图片2"
},
{
src: require("@/assets/imgs/banner3.jpg"),
title: "图片3"
}
],
navList: [
{
src: require("@/assets/imgs/img1.svg"),
title: "段级位考试"
},
{
src: require("@/assets/imgs/img2.svg"),
title: "教练员考试"
},
{
src: require("@/assets/imgs/img3.svg"),
title: "裁判长|员考试"
},
{
src: require("@/assets/imgs/img4.svg"),
title: "考试模拟"
}
]
};
},
methods: {
handleToPage(index) {
switch (index) {
case 0:
this.$router.push({ name: "examinationLevel" });
break;

case 1:
this.$router.push({ name: "coachTest" });
break;

case 2:
this.$router.push({ name: "umpireTest" });
break;

case 3:
this.$router.push({ name: "testSimulating" });
break;

default:
break;
}
},
},
beforeRouteEnter(to, from, next) {
console.log(to, "to");
console.log(from, "from");
next(vm => {
let params = new URLSearchParams();
params.append('code',to.query.code)
vm.$axios.get('/api/wechat/get_weixin_info',{params}).then( res =>{
if(res.data.code === 0){
let token = res.data.data.token

console.log(token)
sessionStorage.setItem("dsToken", token); 
}
})
});
}
};



